import React, { Component } from 'react';
import styled from 'styled-components';

const Title = styled.h1`
    font-weight: 500;
    font-size: 12px;
    line-height: 1;
    margin: 1;
    letter-spacing: 1px;
    text-align: justify;
    color: black;
    padding: 10px 20px;
`;

export class Greeting extends Component {

    render() {
        return (
            <Title>
                <h1>This is a simple weather app that shows the current weather and forecast in the selected city. Choose a city below and find out what kind of weather you will find if you visit there. Click on daily forecast to expand and show more information.</h1>
            </Title>
        )
    }
}